import React from 'react';
import SiteHead from '../../components/SiteHead';
import meta from './meta.json';
import components from './components.json';
import './index.css';

class BrandingPage extends React.Component {
  render() {
    return (
      <div id='brand'>
        <SiteHead
          title={ meta.title }
          description={ meta.description }
        />

        <div className='header'>
          <h1>Logos do ESXBot</h1>
          <p>
            ESXBot é uma marca lúdica e divertida que não se leva muito a sério a sério. 
            Dito isto, estamos empenhados em manter as coisas de qualidade, por isso, se você vai usar nossa arte para algo por favor, mantenha-a com bom gosto e enviar o nosso caminho para aprovação em <a href='mailto:krsolucoesweb@gmail.com' title='Suporte ESXBot'><strong>krsolucoesweb@gmail.com</strong></a> ou no <a href='https://discord.gg/8zGbh3T' title='ESXBot HQ' target='_blank' rel='noopener'><strong>ESXBot HQ</strong></a>. 
            Nós entraremos em contato com você mais rápido que Ganimedes.
          </p>
        </div>

        <div className='container'>
          <div className='brandContent brandLogos'>
            <h3>Nosso logotipo</h3>
            <p>
              Por favor, não edite, altere, distorça, recore ou reconfigure Logotipo da ESXBot.
            </p>
            <div className='brandLogos-main'>
              {
                components.logos.map((logo, i) => {
                  return (
                    <div className='brandLogo' key={ i } style={{
                      backgroundColor: logo.dark ? 'white' : '#151515',
                      backgroundImage: logo.dark
                        ? 'linear-gradient(45deg,#f6f6f6 25%,transparent 0,transparent 75%,#f6f6f6 0,#f6f6f6),linear-gradient(45deg,#f6f6f6 25%,transparent 0,transparent 75%,#f6f6f6 0,#f6f6f6)'
                        : 'linear-gradient(45deg,#2e3033 25%,transparent 0,transparent 75%,#2e3033 0,#2e3033),linear-gradient(45deg,#2e3033 25%,transparent 0,transparent 75%,#2e3033 0,#2e3033)',
                      border: `2px solid rgba(${logo.dark ? '0, 0, 0' : '255, 255, 255'}, .05)`
                    }}>
                      <img src={ logo.image } />
                    </div>
                  );
                })
              }
            </div>
          </div>

          <div className='brandContent brandColors'>
            <h3>Cores ESXBot</h3>
            <p>
              Por favor, use este esquema de cores quando você está fazendo algo ESXBot relacionado.
            </p>
            <div className='brandColorSwatches'>
              {
                components.colors.map((color, i) => {
                  return (
                    <div className='brandColorSwatch' key={ i } style={{
                      backgroundColor: `#${color.hex}`,
                      color: color.dark ? 'white' : 'black',
                      border: `2px solid rgba(${color.dark ? '255, 255, 255' : '0, 0, 0'}, .05)`
                    }}>
                      <div className='brandColorName'>
                        { color.name }
                      </div>
                      <div className='brandColorCodes'>
                        <code>#{ color.hex }</code>
                      </div>
                    </div>
                  );
                })
              }
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default BrandingPage;



